  border-radius: 5px;
  margin-right: 3px;
}
.pedriatric_btn_middle {
  float: right;
  color: #ffffff;
  background-color: #69b128;
  margin-left: 8px;
  border: 1px solid gray;
  padding: 2px 12px;
  border-radius: 5px;
}
.pedriatric_btn_right,
.previous_btn_left {
  float: right;
  color: #ffffff;
  background-color: #69b128;
  margin-left: 8px;
